import React, { useState } from "react";
import "./style.css";
function TopNav({onClickHandler}) {
    const [searchTerm, setSearchTerm] = useState("");
    const updateSearchTerm =(e)=>{
        const {value} = e.target
        setSearchTerm(value)
    }
  return (
<div className="topnav">
  <a className="active" href="#home">News.com</a>
  <div className="search-container">
      <input type="text" placeholder="Search.." value={searchTerm} onChange={updateSearchTerm} name="search"/>
      <button type="submit" onClick={()=>onClickHandler(searchTerm)}><i className="fa fa-search"></i></button>
  </div>
</div>
  );
}
export default TopNav;
