import React, { useEffect, useState } from "react";
import "./style.css";
import TopNav from "../../component/TopNav";
import middleware from "../../middleware";
const { getNews, searchOnKeywords } = middleware;
function Home() {
  const [allItems, setItems] = useState(null);

  useEffect(() => {
    (async function fetchNews() {
      try {
        const response = await getNews();
        const allNews = await response.json();
        setItems(allNews.articles);
      } catch (err) {
        console.error(err);
      }
    })();
  }, []);

  const onClickHandler = (val) => {
    (async function fetchByKeyword() {
      try {
        const response = await searchOnKeywords(val);
        const filteredNews = await response.json();
        setItems(filteredNews.articles);
      } catch (err) {
        console.error(err);
      }
    })();
  };
  const navigatePage = (url) => (window.location.href = url);

  return (
    <>
      <TopNav onClickHandler={onClickHandler} />
      <div className="cards">
        {allItems &&
          allItems.map((el) => {
            return (
              <article
                className="card"
                onClick={() => navigatePage(el.url)}
                key={el.id}
              >
                <img src={el.urlToImage} alt="Nothing here" />
                <header>
                  <h2>{el.title}</h2>
                </header>
                <div className="content">
                  <p>{el.description}</p>
                </div>
              </article>
            );
          })}
      </div>
    </>
  );
}
export default Home;
